


.simple-rating {
    position: relative;
    display: inline-block;
    font-size: 24px;

    &::before {
        content: "★★★★★";
        display: block;
    }

}

.simple-rating__items {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row-reverse;
    overflow: hidden;
    
}

.simple-rating__item {
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0%;
    opacity: 0;
    display: flex;
    flex-direction: row-reverse;
    overflow: hidden;

    &:checked,
    &:checked ~ .simple-rating__label {
        color: #AB816C;

    }
}

.simple-rating__label {
    flex: 0 0 20%;
    height: 100%;
    cursor: pointer;
    color: #FEF3E4;
    

    &::before {
        content: "★";
        display: block;

    }

    &:hover,
    &:hover ~ &,
    &:checked ~ &:hover {
        color: #e4bdaa;

    }

}

